import Vue from 'vue'
import Router from 'vue-router'
import store from './vuex'
import { Dialog } from 'vant'

/* 挂号 */
import UserRegister from './views/appointment' // 挂号首页(Home)
import SelectHospital from './views/appointment/selectHospital/index' // 选择医院列表
// import NotFound from "./views/notFound" // notFound
const NotFound = () => import('./views/notFound')// 404
const ServerError = () => import('./views/serverError') // 500
const NoNetwork = () => import('./views/noNetwork') // 无网络
const SelectDepartment = () => import('./views/appointment/selectDepartment/index') // 选择科室
const DepartmentDetails = () => import('./views/appointment/departmentDetails/index') // 科室详情
const HospitalDetails = () => import('./views/appointment/hospitalDetails/index') // 医院详情
const DoctorDetails = () => import('./views/appointment/doctorDetails/index') // 医生详情
const SelectNum = () => import('./views/appointment/selectNum/index') // 选择号码
const AckDetails = () => import('./views/appointment/ackDetails/index') // 确认详情
const AckPay = () => import('./views/appointment/ackPay') // 确认支付S
const PaySuccess = () => import('./views/appointment/paySuccess') // 预约成功

/* 个人中心 */
const User = () => import('./views/user') // 个人中心
const UserOptions = () => import('./views/user/userOptions') // 个人中心选项列表
const AccountManagement = () => import('./views/user/accountManagement') // 账户管理
const ChangePassword = () => import('./views/user/changePassword') // 修改密码
const Evaluate = () => import('./views/user/evaluate') // 评论
const AppointmentManagement = () => import('./views/user/appointmentManagement') // 预约管理
const MyOrder = () => import('./views/user/myOrder') // 我的订单

/* 登录注册 */
const Login = () => import('./views/login')
const Register = () => import('./views/register')
/* 忘记密码 */

const ForgetPassword = () => import('./views/forgetPassword')

/* 帮助 */
const About = () => import('./views/about/index')
const AboutOptions = () => import('./views/about/aboutOptions') // 帮助选项列表
const UserHelp = () => import('./views/about/userHelp') // 帮助用户帮助
const AboutUs = () => import('./views/about/aboutUs') // 帮助关于我们
const MedicalGuide = () => import('./views/about/medicalGuide') // 帮助就医指南

Vue.use(Router)

const router = new Router({
  mode: 'hash',
  base: process.env.BASE_URL,
  linkActiveClass: 'is-active',
  routes: [{
    // 首页重定向到挂号
    path: '/',
    redirect: '/login'
  },
  {
    path: '/appointment',
    name: 'appointment',
    component: UserRegister,
    redirect: '/appointment/selectHospital',
    children: [{
      // 选择医院
      path: '/appointment/selectHospital',
      name: 'selectHospital',
      component: SelectHospital,
      meta: {
        title: '选择医院'
      }
    }, {
      // 选择科室
      path: '/appointment/selectDepartment/:hospitalId/:hospitalName',
      name: 'selectDepartment',
      component: SelectDepartment,
      meta: {
        title: '选择科室',
        rightTitle: '前往医院'
      }
    }, {
      // 医院详情
      path: '/appointment/hospitalDetails/:hospitalId',
      name: 'hospitalDetails',
      component: HospitalDetails
    }, {
      // 科室详情
      path: '/appointment/departmentDetails/:deptId/:deptName',
      name: 'departmentDetails',
      component: DepartmentDetails,
      meta: {
        title: '挂号医生列表'
      }
    }, {
      // 医生详情
      path: '/appointment/doctorDetails/:doctorId/:doctorName/:doctorLevel/:curDoctorJobTitle',
      name: 'doctorDetails',
      component: DoctorDetails,
      meta: {
        title: '医生详情'
      }
    }, {
      // 选择号码
      path: '/appointment/selectNum/:doctScheduleId/:appointDate/:appointWorkDay',
      name: 'selectNum',
      component: SelectNum,
      meta: {
        title: '选择号码',
        requireAuth: true
      }
    }, {
      // 信息确认
      path: '/appointment/ackDetails/:cardId',
      name: 'ackDetails',
      component: AckDetails,
      meta: {
        title: '确认预约信息'
      }
    }, {
      // 确认支付
      path: '/appointment/ackPay',
      name: 'ackPay',
      component: AckPay,
      meta: {
        title: '确认支付'
      }
    }, {
      // 预约成功
      path: '/appointment/paySuccess',
      name: 'paySuccess',
      component: PaySuccess,
      meta: {
        title: '预约成功'
      }
    }

    ]
  },
  {
    // 个人中心重定向=>显示操作列表
    path: '/user',
    name: 'user',
    component: User,
    meta: {
      requireAuth: true
    },
    redirect: '/user/userOptions',
    children: [{
      // 用户操作列表
      path: '/user/userOptions',
      name: 'userOptions',
      component: UserOptions,
      meta: {
        title: '用户操作列表'
      }
    },
    {
      // 账号管理
      path: '/user/accountManagement',
      name: 'accountManagement',
      component: AccountManagement,
      meta: {
        title: '个人信息'
      }
    },
    {
      // 密码修改
      path: '/user/changePassword',
      name: 'changePassword',
      component: ChangePassword,
      meta: {
        title: '密码修改'
      }
    }, {
      // 预约管理
      path: '/user/appointmentManagement',
      name: 'appointmentManagement',
      component: AppointmentManagement,
      meta: {
        title: '预约管理'
      }
    }, {
      // 我的订单
      path: '/user/myOrder',
      name: 'myOrder',
      component: MyOrder,
      meta: {
        title: '我的订单'
      }
    },
    {
      // 评价
      path: '/user/evaluate',
      name: 'Evaluate',
      component: Evaluate,
      meta: {
        title: '评价医生',
        rightTitle: '更多评价'
      }
    }
    ]
  },
  {
    // 帮助重定向=>显示操作列表
    path: '/about',
    name: 'about',
    component: About,
    redirect: '/about/aboutOptions',
    children: [{
      // 用户操作列表
      path: '/about/aboutOptions',
      name: 'aboutOptions',
      component: AboutOptions,
      meta: {
        title: '帮助操作列表'
      }
    },
    {
      // 用户帮助
      path: '/about/userHelp',
      name: 'userHelp',
      component: UserHelp,
      meta: {
        title: '用户帮助'
      }
    },
    {
      // 关于我们
      path: '/about/aboutUs',
      name: 'aboutUs',
      component: AboutUs,
      meta: {
        title: '关于我们'
      }
    },
    {
      // 就医指南
      path: '/about/medicalGuide',
      name: 'medicalGuide',
      component: MedicalGuide,
      meta: {
        title: '就医指南'
      }
    }

    ]
  },
  {
    // 登录
    path: '/login',
    name: 'login',
    component: Login,
    meta: {
      title: '登录'
    }
  },
  {
    // 注册
    path: '/register',
    name: 'register',
    component: Register,
    meta: {
      title: '注册'
    }
  },
  {
    // 忘记密码
    path: '/forgetPassword',
    name: 'forgetPassword',
    component: ForgetPassword,
    meta: {
      title: '忘记密码'
    }
  },
  {
    // serverError，服务器异常
    path: '/serverError',
    component: ServerError
  },
  {
    // No network， 无网络
    path: '/noNetwork',
    component: NoNetwork
  },
  {
    // notFound
    path: '*',
    component: NotFound
  }
  ]
})

router.beforeEach((to, from, next) => {
  // 设置路由title
  store.commit('gmodule/changeTitle', to.meta.title)
  store.commit('gmodule/changeRightTitle', to.meta.rightTitle)
  console.log(to.meta.rightTitle)
  if (to.matched.some(res => res.meta.requireAuth)) { // 验证是否需要登陆
    if (sessionStorage.getItem('userInfo')) { // 查询本地存储信息是否已经登陆,判断键是否存在
      next()
    } else {
      Dialog.confirm({
        title: '提示',
        message: '<div style="text-align: center;">您还未登录，请先登录</div>'
      }).then(() => {
        next({
          path: '/login', // 未登录则跳转至login页面
          query: {
            redirect: from.fullPath
          } // 登陆成功后回到当前页面，这里传值给login页面，to.fullPath为当前点击的页面
        })
      }).catch(() => {})
    }
  } else {
    next()
  }
})

export default router



