const state = {
  curBtnState: false, // 当前按钮状态 true:busy false:normal
  hospitalList: [], // 当前医院列表
  curHospitalId: '', // 当前医院id
  curHospitalName: '', // 当前医院名称
  curHospitalDetails: {}, // 当前医院的详细信息
  curDeptList: [], // 当前医院对应的科室列表
  curDeptId: '', // 当前科室id
  curDeptName: '', // 当前科室名称
  curTimeList: '', // 当前科室的排班时间列表
  curDoctorDutyList: [], // 当前科室当前时间的医生排班列表
  curDoctorId: '', // 当前医生id
  curDoctorName: '', // 当前医生名称
  curDoctorLevel: '', // 当前医生等级 1专家号 0普通号
  curDoctorWork: {}, // 当前医生信息以及排班信息
  curDoctorComment: {
    list: [], // 展示的评论列表
    limit: '5', // 每页展示的条目数
    page: 1, // 当前要请求的页数
    totalPage: 1, // 总共的页数
    isLoading: false
  }, // 当前医生的评论列表
  curLeftSignTime: [], // 当前医生的剩余放号列表
  curAppointmentDate: '', // 当前预约日期 2018-12-10
  curAppointmentStartTime: '', // 当前预约的开始时间 9:55
  curAppointmentEndTime: '', // 当前预约的结束时间 10:55
  curAppointmentWeek: '', // 当前预约周几 周一
  curFee: '10', // 当前挂号费用
  curAppointmentNumId: '', // 当前预约号码的ID
  curDoctScheduleId: '', // 当前的医生排班的id doctScheduleId
  curAppointmentNum: '', // 当前预约的号码
  curBillCode: '' // 当前的订单号
}
export default state



