// 获取医生的评价信息
export const getDoctorCommentByDoctorId = ({ commit }, obj) => {
    ajaxPost(apiUrl.showDoctorCommentByDoctorId, {
            doctorId: obj.doctorId,
            limit: obj.limit,
            page: obj.page.toString()
        },
        (res) => {
            const resData = res.data.body // 返回的评论信息 评论列表 页码详情
            console.log(resData)
            commit('changeDoctorCommentList', resData)
        })
}

// 获取当前医生的剩余放号
export const getLeftSignTime = ({
    commit
}, obj) => {
    ajaxPost(apiUrl.getLeftSignTime, {
        doctScheduleId: obj.doctScheduleId,
        aorp: obj.aorp
    }, (res) => {
        const resData = res.data.body.data //
        console.log(resData)
        commit('changeLeftSignTime', resData)
    })
}

// 点击确认信息按钮获取订单号
export const getBillCode = ({
    commit
}, obj) => {
    console.log(obj, 'a')
        // ajaxPost('http://192.168.50.128:7083/aidoct-portal/h5/bill/add', {
    ajaxPost(apiUrl.getBillCode, {
        fee: obj.fee,
        hospId: obj.hospId,
        hospName: obj.hospName,
        deptId: obj.deptId,
        deptName: obj.deptName,
        doctId: obj.doctId,
        doctName: obj.doctName,
        userId: obj.userId,
        createUserId: obj.createUserId,
        updateUserId: obj.updateUserId,
        doctorSignTimeId: obj.id,
        doctorScheduleId: obj.doctorScheduleId,
        remark: '测试',
        tradeType: '1', // 交易类型
        payType: '1' // 支付

    }, (res) => {
        const resData = res.data.body.data //
        console.log(resData)
        commit('changeBillCode', resData)
        commit('changeBtnState', false)
    })
}


