<template>
  <div id="headerTop">
    <div class="historyBack" @click="backTo" v-if="showBack">返回</div>
    <div style="color: #3391E8;" v-else>返回</div>
    <div class="title" v-show="showTitle">{{title}}</div>
<!--     <router-link class="rightShow" @click= :to="{name:'hospitalDetails',params:{hospitalId:hospitalId}}" tag="div" v-if="showHeadRight">医院简介</router-link> -->
    <div class="rightShow" v-if="showHeadRight" @click="changeRouter">{{titleRight}}</div>
    <div v-else class="rightShowSpc" style="color: #3391E8;">医院简介</div>
  </div>
</template>
<script>
import store from '@/vuex/index'
export default {
  name: 'headerH',
  props: ['title', 'showBack', 'showTitle', 'showHeadRight','titleRight'],
  data () {
    return {
    }
  },
  methods: {
    backTo: function () {
      this.$emit('back')
    },
    changeRouter:function(){
      this.$emit('rightRouter');
    },
    toHospitalDetails () {
      this.$router.push({name: 'hospitalDetails', params: {hospitalId: store.state.wmodule.curHospitalId}});//天宇前往医院
    }
  }
}
</script>

<style scoped lang="less">
    #headerTop{
        position: fixed;
        box-sizing: border-box;
        left: 0;
        top: 0;
        width: 100%;
        height: 0.44*2rem;
        background-color: #3391E8;
        display: flex;
        z-index: 3;
        div{
            width: 100%;
            height: 0.44*2rem;
            line-height: .44*2rem;
            box-sizing: border-box;
            color: #fff;
            flex: 1;
        }
        .historyBack{
            background: url('../assets/header/nav_back@2x.png') no-repeat .15*2rem center;
            background-size: 0.06*2rem 0.12*2rem;
            line-height: .44*2rem;
            line-height:normal;
            padding: 0.12*2rem 0;
            font-size: 0.14*2rem;
            text-indent: 0.3*2rem;
        }
        .title{
            text-align: center;
            font-size: 0.15*2rem;
            font-family: 'PingFangSC-Medium';
            flex: 2;
            overflow: hidden;    
            text-overflow:ellipsis;    
            font-weight: 520;
        }
        .rightShow{
            background: url('../assets/header/nav_rightlink@2x.png') no-repeat 0.71*2rem center;
            background-size: 0.06*2rem 0.12*2rem;
            font-size: 0.14*2rem;
            line-height: normal; 
            padding: 0.12*2rem 0; 
            text-indent: 0.1*2rem;
        }
    }
</style>



