<template>
  <div v-cloak>
    <hospital-header
      :title="title"
      :titleRight="titleRight"
      :showTitle="showTitle"
      :showBack="showBack"
      :showHeadRight="showHeadRight"
      @back="routerBack"
      @rightRouter="changeRouter"
      v-show="isShowHeader"
      ref="hospitalHeader"
    ></hospital-header>
    <div class="view">
      <router-view/>
    </div>
    <hospitalFooter :showAll="showFooterAll"></hospitalFooter>
  </div>
</template>

<script>
import hospitalHeader from "@/components/header";
import hospitalFooter from "@/components/footer";
import store from "@/vuex/index";
export default {
  name: "index",
  data() {
    return {
      title: "", // title参数
      titleRight: "", //右边title参数
      showBack: true, // 是否展示返回按钮
      showTitle: true, // 是否展示标题
      showHeadRight: false, // 是否展示右侧跳转
      showFooterAll: true, // 是否显示footer导航
      isShowHeader: true // 是否显示头部 在医院详情界面不需要显示头部
    };
  },
  created() {
    // 设置title
    this.title = this.$store.state.gmodule.title;
    this.titleRight = this.$store.state.gmodule.rightTitle;
    // console.log(this.title);
  },
  updated() {
    // 设置title
    this.title = this.$store.state.gmodule.title;
    this.titleRight = this.$store.state.gmodule.rightTitle;
    // console.log(this.title);
  },
  mounted() {
    // console.log('this'+this.title)
  },
  methods: {
    routerBack: function() {
      this.$router.go(-1); // 路由回退
    },
    changeRouter: function() {
      if (
        this.$router.history.current.fullPath.indexOf("/user/evaluate") === 0
      ) {
        //目前处于评价医生页面
        console.log(store.state.gmodule.curEvaluateDetail);
        this.$router.push({
          name: "doctorDetails",
          params: {
            doctorId: store.state.gmodule.curEvaluateDetail.doctId,
            doctorName: store.state.gmodule.curEvaluateDetail.doctName,
            doctorLevel: store.state.gmodule.curEvaluateDetail.isExpert, //普通号0 专家号1
            curDoctorJobTitle: store.state.gmodule.curEvaluateDetail.jobTitle //职称
          }
        }); //前往医生评论
      } else {
        //处于前往医院
        this.$router.push({
          name: "hospitalDetails",
          params: { hospitalId: store.state.wmodule.curHospitalId }
        }); //天宇前往医院
      }
    }
  },
  watch: {
    $route(now, old) {
      if(now.path.indexOf('/appointment/hospitalDetails') > -1 || now.path === '/appointment/paySuccess'){
        this.isShowHeader = false
      }else{
        this.isShowHeader = true
      }
      // 监控路由变换，控制返回按钮的显示
      if (
        now.path.indexOf("/appointment/selectDepartment") === 0 ||
        now.path.indexOf("/user/evaluate") === 0
      ) {
        // 只要包含该路由就是医院选择科室界面
        // 显示<医院简介>按钮
        this.showHeadRight = true;
      } else {
        this.showHeadRight = false;
      }
      if (now.path === "/appointment/paySuccess") {
        this.showBack = false;
        this.showTitle = false;
      } else {
        this.showBack = true;
        this.showTitle = true;
      }
      if (
        now.path === "/login" ||
        now.path === "/register" ||
        now.path.indexOf("/appointment/ackDetails") === 0 ||
        now.path.indexOf("/appointment/ackPay") === 0
      ) {
        this.showFooterAll = false;
      } else {
        this.showFooterAll = true;
      }
    }
  },
  components: {
    hospitalHeader,
    hospitalFooter
  }
};
</script>

<style scoped lang="less">
[v-cloak] {
  display: none;
}

* {
  box-sizing: border-box;
  background: #fff;
}

.view {
  position: relative;
  margin-top: 0.44 * 2rem;
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s ease;
}

.fade-enter,
.fade-leave-active {
  opacity: 0;
}
</style>



