const orderList = function() {
	let data = [];
	for(let i = 0 ; i<1 ;i++){
		let newArticleObject = {
			"ordernumber": "cz20180529112233369",
			"numbertype": parseInt(Math.random()*3),
			"createTime": "Dec 25, 2018 6:54:00 PM",
			"cost": '10元',
			"id": i,
			"time": Random.date() + ' ' + Random.time(),
			"status": 0,
			"tjs": 1
		}
		data.push(newArticleObject)
	}
	return {
		data
	}
}


//就诊信息数据mockck

const appointList = function() {
	let data = [];
	for(let i = 0 ; i<10 ;i++){
		let newArticleObject = {
			"id": parseInt(Math.random()*200),
			"doctor": Random.csentence(3, 5),
			"signState": parseInt(Math.random()*3),
			"appointType": parseInt(Math.random()*3),
			"images":Random.dataImage('300x250', 'mock的图片'),
			"hospital": Random.csentence(5, 10),
			"time": "2018-12-10 周一 上午 08:55",
			"name": Random.csentence(3, 5),
			"evaluate": parseInt(Math.random()*3),
			"userId": parseInt(Math.random()*300),
			"doctId": parseInt(Math.random()*300),
			"doctName": Random.csentence(5, 10),
			"feeType": parseInt(Math.random()*300),
			"hospName":  Random.csentence(5, 10)
		}
		data.push(newArticleObject)
	}
	return {
		data
	}
}

Mock.mock('/api/getAppoint', appointList);

Mock.mock('/api/getOrder', orderList);

Mock.mock('/api/getCode', code);

Mock.mock('/api/getUsers', personMsg);






