export const add = (state, obj) => {
  state.count += obj;
  console.log('state.count'+state.count)
};

//修改banner中的数据
export const changeBannerData = (state, obj) => {
  state.bannerData = obj;
};

//修改列表商品数据
export const changeHomeList = (state, obj) => {
  state.homeList = obj;
};

//修改列表页商品数据
export const changeProductList = (state, obj) => {
  state.productList = state.productList.concat(obj);
};

//实现删除商品功能，列表页
export const removeProduct = (state, obj) => {
  //目的是把state.productList中的当前项删除掉
  //obj=>this.item
  state.productList = state.productList.filter((item) => {
    return item !== obj;
  });
};

//实现增加商品的功能，添加页
export const addProduct = (state, obj) => {
  //给productList列表中增加一项商品
  state.productList.unshift(obj);
};

//给收藏列表添加一项
export const addCollect = (state, obj) => {
  //判断列表中是否已经存在该项，如果有就不再添加了
  state.collectList.indexOf(obj) === -1 ? state.collectList.unshift(obj) : null;
};

//给收藏列表删除一项
export const removeCollect = (state,obj)=>{
  state.collectList = state.collectList.filter((item) => {
    return item !== obj;
  });
};

//修改注册数据
export const changeRegisterVal = (state, obj) => {
	state.register[obj.name] = obj.val;
}




