<template>
  <ul class="hospitalItemList">
    <router-link
      class="hospitalItem"
      v-for="item in hospitalList"
      :key="item.id"
      tag="li"
      :to="{name:'selectDepartment',params:{hospitalId:item.id,hospitalName:item.hospitalName}}" @click.native="()=>{storeHospital(item.id,item.hospitalName)}">
      <div class="left">
        <img src="../../../assets/hospital.jpg" alt="医院图片">
      </div>

      <div class="right">
        <div class="hospitalName">{{item.hospitalName}}</div>
        <div class="tag">
          <span class="level" v-if="item.hospitalLevel==1">
            三级甲等
            </span>
          <span class="level" v-else-if="item.hospitalLevel==2">
            二级甲等
            </span>
          <span class="category"  v-if="item.hospitalType==1">
            公立综合
            </span>
        </div>
      </div>
    </router-link>
  </ul>
</template>

<script>
import store from '@/vuex/index'
export default {
  name: "hospitalItem",
  data() {
    return {
    };
  },
  methods: {
    //将医院的id和名称存储到Vuex中
    storeHospital(hospitalId,hospitalName) {
      store.commit('wmodule/changeHospital',{hospitalId:hospitalId,hospitalName:hospitalName})
      // console.log(store.state.wmodule);
    }
  },
  created() {
    //请求医院列表数据
    store.dispatch('wmodule/getAllHospital')
  },
  computed:{
    //因为请求时异步的，所以在计算属性中随时更新
    hospitalList(){
      return store.state.wmodule.hospitalList;
    }
  }
};
</script>

<style scoped lang="less">
.hospitalItemList {
  padding-top: unit(0.432*2,rem);
  padding-bottom: unit(0.49*2,rem);
  .hospitalItem {
    box-sizing: border-box;
    /*height: unit(0.888*2,rem);*/
    height: auto;
    padding: unit(0.16*2,rem) 0 0 unit(0.15*2,rem);
    display: flex;
    .left {
      width: unit(0.98*2,rem);
      height: unit(0.64*2,rem);
      border-radius: unit(0.04*2,rem);
      overflow: hidden;
      img {
        display: block;
        width: 100%;
        height: 100%;
      }
    }
    .right {
      // float: right;
      margin-left: unit(0.1*2,rem);
      width: unit(2.53*2,rem);
      border-bottom: unit(0.01*2,rem) solid #f6f7f8;
      position: relative;
      .hospitalName {
        padding-right: unit(.5*2,rem);
        font-family: PingFangSC-Medium, sans-serif;
        font-size: unit(0.15*2,rem);
        line-height: unit(0.21*2,rem);
      }
      .tag {
        /*position: absolute;*/
        bottom: unit(0.11*2,rem);
        margin: unit(.2*2,rem) 0 unit(.11*2,rem) 0;
        span {
          // display: flex;
          // flex-direction: row;
          // justify-content: center;
          padding: unit(0.03*2,rem) unit(0.08*2,rem);
          background: #f1f1fb;
          border-radius: unit(0.02*2,rem);
          font-size: unit(0.1*2,rem);
          font-family: PingFangSC-Regular, sans-serif;
          // vertical-align: middle;
          // line-height: unit(.145*2,rem);
          // height: unit(.145*2,rem);
          display: inline-block;
          &.level {
            color: #ff941f;
          }
          &.category {
            color: #ff7960;
          }
        }
      }
    }
  }
}
</style>



