// 修改头部中间标题
export const changeTitle = (state, obj) => {
  state.title = obj
}
// 修改头部右边标题
export const changeRightTitle = (state, obj) => {
  state.rightTitle = obj
}
// 修改评价详细内容
export const changeEvaluateDetail = (state, obj) => {
  state.curEvaluateDetail = obj
  // console.log("vuex",obj);
  var objString = JSON.stringify(obj)
  window.localStorage.setItem('gmoduleEvaluate', objString)
  console.log('vuexType', typeof obj)
}



