<template>
  <div class="hospitalFilter">
    <span class="location">
      <span>黄镇</span>
      <i>
        <img src="../../../assets/appointment/seldoc_pulldown@2x.png">
      </i>
    </span>
    <span class="border"></span>
    <span class="level">
      <span>三甲</span>
      <i>
        <img src="../../../assets/appointment/seldoc_pulldown@2x.png">
      </i>
    </span>
  </div>
</template>

<script>
export default {
  name: "hospitalFilter"
};
</script>

<style scoped lang="less">
.hospitalFilter {
  box-sizing: border-box;
  width: 100%;
  position: fixed;
  height: 0.432*2rem;
  background: #fff;
  padding: 0.08*2rem 0.21*2rem 0.067*2rem;
  display: flex;
  line-height: 0.285*2rem;
  border-bottom: 0.01*2rem solid #f6f7f8;
  z-index: 100;
  .location,
  .level {
    flex: 1;
    font-size: 0.14*2rem;
    color: #b1afaf;
    position: relative;

    span {
      position: absolute;
      letter-spacing: 0.04*2rem;
      left: 50%;
      transform: translateX(-50%);
      font-family: PingFangSC-Regular, sans-serif;
    }

    i {
      position: absolute;
      right: 0;
      width: 0.08*2rem;
      height: 0.04*2rem;
      top: 50%;
      transform: translateY(-50%);
      img {
        display: block;
        width: 100%;
        height: 100%;
      }
    }
  }

  .border {
    width: 0.01*2rem;
    height: 0.285*2rem;
    background: #f6f7f8;
    margin: 0 0.2*2rem;
  }
}
</style>



