<template>
  <ul id="footer" v-show="showAll">
    <router-link to="/about" tag="li">帮助</router-link>
    <router-link to="/appointment" tag="li">预约挂号</router-link>
    <router-link to="/user" tag="li">我的</router-link>
  </ul>
</template>

<script>
export default {
  name: "footerH",
  data() {
    return {};
  },
  props: ["showAll"]
};
</script>

<style scoped lang="less">
#footer {
  box-sizing: border-box;
  display: flex;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 0.5*2rem;
  background: #fcfcfc;
  justify-content: space-around;
  overflow: hidden;
  font-size: 0.1*2rem;
  border-top: 2px solid #f4f4f4;
  li {
    width: 100%;
    height: 100%;
    line-height: 0.77*2rem;
    text-align: center;
    color: #272727;
  }
  li {
    &:first-of-type {
      background: url("../assets/footer/Tab_help_pre@2x.png") no-repeat center
        0.07*2rem;
      background-size: 0.21*2rem 0.21*2rem;
    }
    &:nth-of-type(2) {
      background: url("../assets/footer/Tab_registration_pre@2x.png") no-repeat
        center 0.048*2rem;
      background-size: 0.273*2rem 0.273*2rem;
    }
    &:last-of-type {
      background: url("../assets/footer/Tab_info_pre@2x.png") no-repeat center
        0.07*2rem;
      background-size: 0.21*2rem 0.21*2rem;
    }
  }
  li.is-active {
    &:first-of-type {
      background: url("../assets/footer/Tab_help@2x.png") no-repeat center
        0.07*2rem;
      background-size: 0.21*2rem 0.21*2rem;
    }
    &:nth-of-type(2) {
      background: url("../assets/footer/Tab_registration@2x.png") no-repeat
        center 0.048*2rem;
      background-size: 0.273*2rem 0.273*2rem;
    }
    &:last-of-type {
      background: url("../assets/footer/Tab_info@2x.png") no-repeat center
        0.07*2rem;
      background-size: 0.21*2rem 0.21*2rem;
    }
  }
}
</style>



