import Vue from 'vue'
import Vuex from 'vuex'
import gmodule from './modules/gmodule'
import pmodule from './modules/pmodule'
import wmodule from './modules/wmodule'

Vue.use(Vuex)

const debug = process.env.NODE_ENV !== 'production'

export default new Vuex.Store({
  modules: {
    gmodule,
    pmodule,
    wmodule
  },
  strict: debug,
})


