import axios from 'axios'
/* //全称
export const addFn = (context,obj)=>{

}; */
//简写，解构赋值
export const addFn = ({
  commit
}, obj) => {
  console.log('actions',obj)
  commit('add', obj);
};
//请求首页banner数据
export const getBannerData = ({
  commit
}, obj) => {
  axios.get("/bannerlist").then(data => {
    console.log('bannerlist🆗');

    commit('changeBannerData', data.data);
  });
}
//请求首页商品数据
export const getHomeList = ({
  commit
}, obj) => {
  axios.post('/homelist').then(data => {
    console.log('homelist🆗');

    commit('changeHomeList', data.data);
  })
}
//列表页商品数据
export const getProductList = ({
  commit
}, obj) => {
  axios.post('/productlist', {
    params: {
      name: 'a',
      age: 8
    }
  }).then((data) => {
    console.log(data);
    commit('changeProductList',data.data)
  })
}




