import axios from 'axios'
import {
  apiUrl
} from './api'
import qs from 'qs'

axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded'

// 请求拦截器
axios.interceptors.request.use(function (config) {
  return config
}, function (error) {
  return Promise.reject(error)
})
// 响应拦截器
axios.interceptors.response.use(function (response) {
  return response
}, function (error) {
  return Promise.reject(error)
})

function getPostParams (url, subbody) {
  subbody = subbody || {}
  const requestType = url.substr(url.lastIndexOf('/') + 1)
  // var loginUserId = '288a85f20a874aeca67e03d3bbddd4ac';
  var loginUserId = ''
  if (sessionStorage.getItem('userId')) {
    loginUserId = sessionStorage.getItem('userId')
  }
  subbody.loginUserId = loginUserId
  const postParams = {
    jsonParams: {
      header: {
        requestType: requestType,
        loginUserId: loginUserId,
        macNo: 'XXXXXXX',
        appVersionCode: '1.2',
        appLang: '',
        appChannel: '',
        appDeviceType: '',
        interfaceVersion: '2'
      },
      body: subbody
    }
  }
  postParams.jsonParams = JSON.stringify(postParams.jsonParams)
  // postParams.jsonParams = Encrypt(JSON.stringify(postParams.jsonParams));
  postParams.sign = sign(postParams.jsonParams)
  // postParams.sign = '1';
  postParams.requestSource = '1'
  return postParams
}
// 签名hash
function sign (str) {
  var hash = 20080808
  for (var i = 0, len = str.length; i < len; ++i) {
    hash ^= (hash << 5) + str.charAt(i).charCodeAt() + (hash >> 2)
  }
  return hash & 0x7fffffff
}

function setLoginUserInfo (res) {
  // var userInfo={
  //     username:res.eglishName,
  //     headImg:res.headImg,
  //     isLeader:res.isLeader,
  //     id:res.id,
  //     languageSwitch:res.languageSwitch,
  //     companyId:res.companyId,
  //     companyName:res.company,
  //     depts:res.depts
  // };
  // sessionStorage.setItem("userInfo",JSON.stringify(userInfo));
  sessionStorage.setItem('userInfo', JSON.stringify({
    id: res.id,
    username: res.username
  }))
}
// 发送请求及接收后处理
export const ajaxPost = (url, params, callback, errorCallback) => {
  axios.post(url, qs.stringify(getPostParams(apiUrl.login, params)))
    .then(function (response) {
      var _res = response.data.body
				//if (_res.resultCode === '1000') {
			callback(response)
      // } else {
      // topTip('error', res.resultDesc)
      // }
    })
    .catch(function (error) {
      errorCallback(error)
      console.log('服务器错误', error);
	})
}



