// 修改当前的医院列表
export const changeHospitalList = (state, payload) => {
  state.hospitalList = payload
}
// 修改当前医院对应的科室列表
export const changeDeptList = (state, payload) => {
  state.curDeptList = payload
}
// 修改当前所选择的医院id和名称
export const changeHospital = (state, payload) => {
  state.curHospitalId = payload.hospitalId
  state.curHospitalName = payload.hospitalName
}

// 修改当前医院的详细信息
export const changeHospitalDetails = (state, payload) => {
  state.curHospitalDetails = payload
}
// 修改当前的科室id和名称
export const changeDept = (state, payload) => {
  state.curDeptId = payload.deptId
  state.curDeptName = payload.deptName
}

// 修改当前科室的时间排班信息
export const changeTimeList = (state, payload) => {
  state.curTimeList = payload
}

// 修改当前科室当前时间的医生排班列表
export const changeDoctorDutyList = (state, payload) => {
  state.curDoctorDutyList = payload
}

// 修改当前医生的评价列表
export const changeDoctorCommentList = (state, payload) => {
  let list = state.curDoctorComment.list.concat(payload.data.commentList)
  // 防止重复请求出现重复的评论，所以做一个数组去重
  for (let i = 0; i < list.length; i++) {
    for (let j = i + 1; j < list.length; j++) {
      if (list[i].id === list[j].id) {
        list[j] = list[list.length - 1]
        list.length--
        i--
      }
    }
  }
  state.curDoctorComment.list = list
  state.curDoctorComment.totalPage = payload.page.totalPage
  state.curDoctorComment.page += 1
  state.curDoctorComment.isLoading = false
}
// 将医生评论列表的page重置为1
export const resetPage = (state, payload) => {
  state.curDoctorComment.page = 1
}

// 修改当前选择的医生的id,名称
export const changeDoctor = (state, payload) => {
  state.curDoctorId = payload.doctorId
  state.curDoctorName = payload.doctorName
  state.curDoctorLevel = payload.doctorLevel
}

// 修改当前预约信息中的时间(日期，周几，时间，预约号码,当前号码的id)
export const changeAppointmentDate = (state, payload) => {
  state.curAppointmentDate = payload.appointDate
  state.curAppointmentWeek = payload.appointWorkDay
  state.curAppointmentStartTime = payload.signStartTime
  state.curAppointmentEndTime = payload.signEndTime
  state.curAppointmentNum = payload.signCode
  state.curAppointmentNumId = payload.id
  state.curDoctScheduleId = payload.doctScheduleId
}

// 修改当前医生排班信息
export const changeDoctorWork = (state, payload) => {
  state.curDoctorWork = payload
}

// 修改当前预约的医生的挂号费用
export const changeFee = (state, payload) => {
  state.curFee = payload
  // state.curFee = '10'
}

// 修改当前医生的剩余放号列表
export const changeLeftSignTime = (state, payload) => {
  state.curLeftSignTime = payload
}

// 修改当前的订单编号
export const changeBillCode = (state, payload) => {
  state.curBillCode = payload
}

// 修改当前的btn状态
export const changeBtnState = (state, payload) => {
  state.curBtnState = payload
}



